.Selector {
  text-align: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.Selector p {
  color: #ffffff;
  font-size: 2rem;
  -webkit-animation: moveInTop 0.5s ease-out 0.75s;
          animation: moveInTop 0.5s ease-out 0.75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.Selector select,
.Selector input {
  width: 16rem;
  background-color: #ffffff;
  color: #777777;
  padding: 1.1rem 2.5rem;
  display: inline-block;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  -webkit-animation: moveInBottom 0.5s ease-out 0.75s;
          animation: moveInBottom 0.5s ease-out 0.75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
  margin-left: 0.5rem;
  font-size: 1.7rem;
  text-align: center;
}
/* Class for preventing rage clicking */
.Disabled {
  pointer-events: none;
  cursor: default;
}
/* Submit button in header section */
.Btn {
  background-color: #ffffff;
  color: #777777;
  -webkit-animation: moveInBottom 0.5s ease-out 0.75s;
          animation: moveInBottom 0.5s ease-out 0.75s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
}
.Btn:link,
.Btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 0.75rem 2rem;
  display: inline-block;
  border-radius: 10rem;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
  position: inherit;
  margin-left: 2rem;
  font-size: 1.6rem;
  -webkit-transform: translate(0);
      -ms-transform: translate(0);
          transform: translate(0);
}
.Btn:hover,
.Selector select:hover,
.Selector input:hover {
  -webkit-transform: translateY(-0.3rem);
      -ms-transform: translateY(-0.3rem);
          transform: translateY(-0.3rem);
  -webkit-box-shadow: 0 1rem 2rem rgba(255, 255, 255, 0.3);
          box-shadow: 0 1rem 2rem rgba(255, 255, 255, 0.3);
}
.Btn:active {
  -webkit-transform: translateY(-0.1rem);
      -ms-transform: translateY(-0.1rem);
          transform: translateY(-0.1rem);
  -webkit-box-shadow: 0 0.5rem 1rem rgba(255, 255, 255, 0.3);
          box-shadow: 0 0.5rem 1rem rgba(255, 255, 255, 0.3);
}
.Btn::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 10rem;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  background-color: #ffffff;
}
.Btn:hover::after {
  -webkit-transform: scaleX(1.4) scaleY(1.6);
      -ms-transform: scaleX(1.4) scaleY(1.6);
          transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

@-webkit-keyframes moveInBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(3rem);
            transform: translateY(3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}
@-webkit-keyframes moveInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}
@keyframes moveInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}



