.Header {
    height: 95vh;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85vh, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 85vh, 0 100%);
    position: relative;
    overflow: hidden;
}
.BgVideo {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}
.BgVideo video {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.BgVideoOverlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: .35;
    background-size: cover;
    background-position: center;
}
.LogoBox {
    position: absolute;
    top: 3rem;
    left: 3rem;
}
.LogoBox img {
    height: 5rem;
}

.TextBox {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}
.TextBox h1 {
    color: #ffffff;
    text-transform: uppercase;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    margin-bottom: 5rem;
}
.HeadingMain {
    display: block;
    font-size: 7rem;
    font-weight: 400;
    letter-spacing: 3.5rem;
    line-height: 1.2;
    -webkit-animation: moveInLeft 1s ease-out;
            animation: moveInLeft 1s ease-out;
    text-align: center;
    margin-bottom: 2rem;
    padding-left: 2rem;
}
.HeadingSub {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: .95rem;
    -webkit-animation: moveInRight 1s ease-out;
            animation: moveInRight 1s ease-out;
}
/* Scroll gif in header*/
.Header a {
    position: absolute;
    left: 50%;
    bottom: 2.5%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.Header a img {
    height: 6.5rem;
    display: none;
}

@-webkit-keyframes moveInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }
    80% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }
    80% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
@-webkit-keyframes moveInRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }
    80% {
        -webkit-transform: translateX(-1rem);
                transform: translateX(-1rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}
@keyframes moveInRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }
    80% {
        -webkit-transform: translateX(-1rem);
                transform: translateX(-1rem);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}


